import React from 'react';
// import '../css/logout.css';
import { Link } from 'gatsby';

function logout() {
    localStorage.clear();

    const screen = (
        <>
            <div id="frame">
                <h1>Are you sure you wish to logout?</h1>
                <button><Link to="/login">Yes</Link></button>
                <button><Link to="/menu">No</Link></button>
            </div>
        </>
    );
    return(screen);
}

export default logout;